<template>
  <div class="dropdown">
    <div class="dropdown__selected" @click="toggle">
      {{ selected }}
      <span class="material-icons material-icons__arrow">arrow_drop_down</span>
    </div>
    <div v-if="show == true">
      <ul class="dropdown__items">
        <li
          @click="setLanguage(language.lang)"
          href="#"
          v-for="language in languages"
          :key="language.id"
        >
          {{ language.lang }}
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  name: "Dropdown",
  data() {
    return {
      show: false
    };
  },
  computed: {
    ...mapState({
      languages: state => state.language,
      selected: state => state.selectedLang
    })
  },
  methods: {
    ...mapActions(["setData", "destructure", "emptyFiltered"]),
    toggle() {
      this.show = !this.show;
    },
    setLanguage(lang) {
      this.show = false;
      this.emptyFiltered();
      this.setData(lang);
      this.destructure();
    }
  }
};
</script>
